<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="card-body">
    <section class="mb-4">
      <!-- type -->
      <div class="mb-4">
        <h5>TYPE：</h5>
        <template v-for="(item, index) in types" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-type"
            :id="`type${index}`"
            autocomplete="off"
            :value="item"
            v-model="orderOption.type"
          />
          <label class="btn border-0 position-relative me-4" :for="`type${index}`"
            >{{ item }}
            <CircleTwo :class="{ 'd-none': orderOption.type !== item }" />
          </label>
        </template>
      </div>
      <!-- flavor -->
      <div class="mb-4">
        <h5>FLAVOR：</h5>
        <template v-for="(item, index) in productList" :key="index">
          <input
            type="radio"
            class="btn-check"
            name="options-flavor"
            :id="`flavor${index}`"
            autocomplete="off"
            :value="item.title"
            v-model="orderOption.flavor"
            @click="tempProduct = item"
          />
          <label
            class="btn border-0 position-relative"
            v-if="item.category === orderOption.type"
            :for="`flavor${index}`"
            >{{ item.title }}
            <CircleOne :class="{ 'd-none': item.title !== orderOption.flavor }" />
          </label>
        </template>
      </div>
      <!-- size -->
      <div class="mb-4">
        <h5>SIZE：</h5>
        <template v-for="(item, index) in sizeList" :key="index">
          <input
            type="radio"
            class="btn-check me-md-4"
            name="options-size"
            :id="`size${index}`"
            autocomplete="off"
            :value="item._id"
            v-model="orderOption.size"
            @click="
              orderOption.price = item.price;
              orderOption.quantity = 1;
            "
          />
          <label class="btn border-0 position-relative me-3" :for="`size${index}`"
            >{{ item.size }}
            <CircleThree :class="{ 'd-none': item._id !== orderOption.size }" />
          </label>
        </template>
      </div>
      <!-- price -->
      <div class="mb-4">
        <h5>PRICE：</h5>
        <p class="ms-3">$ <span class="text-secondary">{{ orderOption.price }}</span> / EACH</p>
      </div>
      <!-- 數量＆＄ -->
      <div class="mb-4">
        <h5 class="text-uppercase">quantity：</h5>
          <nav aria-label="..." class="me-4">
            <ul class="pagination">
              <li class="page-item">
                <a
                  class="page-link rounded-0"
                  href="#"
                  @click.prevent="orderOption.quantity--"
                  :class="{ disabled: orderOption.quantity <= 1 }"
                  ><i class="bi bi-dash"></i
                ></a>
              </li>
              <li class="page-item">
                <input
                  type="text"
                  class="page-link"
                  style="width: 2.9em"
                  v-model="orderOption.quantity"
                />
              </li>
              <li class="page-item">
                <a
                  class="page-link rounded-0"
                  href="#"
                  @click.prevent="orderOption.quantity++"
                  :class="{ disabled: orderOption.quantity >= 10 }"
                  ><i class="bi bi-plus-lg"></i
                ></a>
              </li>
            </ul>
          </nav>
      </div>
      <!-- 總金額 -->
      <div class="">
        <h5 class="text-uppercase">total：</h5>
        <div class="fst-italic ms-3 h4 text-secondary luluText">
          ${{ this.$toCurrency(orderOption.price * orderOption.quantity) }}
        </div>
      </div>
    </section>
    <button class="btn btn-outline-primary rounded-0 mb-3" @click="addCart">
      <i class="bi bi-caret-right-fill"></i>Add to Chart
    </button>
  </div>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 5rem;
}
.circle-2 {
  width: 5rem;
}
.circle-3 {
  width: 5rem;
}
</style>

<script>
import { apiGetProducts, apiPostCart } from '@/assets/javascript/api';
import CircleOne from '../models/circle/CircleOne.vue';
import CircleTwo from '../models/circle/CircleTwo.vue';
import CircleThree from '../models/circle/CircleThree.vue';

export default {
  data() {
    return {
      tempProduct: {},
      productList: [],
      flavorList: [],
      sizeList: [],
      types: ['pie', 'cake', 'cookie'],
      orderOption: {
        type: 'pie',
        flavor: '',
        size: '',
        price: 0,
        quantity: 1,
      },
    };
  },
  components: {
    CircleOne,
    CircleTwo,
    CircleThree,
  },
  watch: {
    // eslint-disable-next-line object-shorthand
    'orderOption.type'() {
      this.selectFlavor();
    },
    // eslint-disable-next-line object-shorthand
    'orderOption.flavor'() {
      this.selectSize();
    },
    tempProduct() {
      this.$emitter.emit('infoData', this.tempProduct);
    },
  },
  methods: {
    getProducts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetProducts()
        .then((res) => {
          this.productList = res.data.data;
          this.selectFlavor();
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    addCart() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      const data = { productId: this.tempProduct._id, option: this.orderOption };
      apiPostCart(data)
        .then((res) => {
          console.log(res);
          this.$emitter.emit('loadingStatus', false);
          this.$emitter.emit('getCatrs');
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    selectFlavor() {
      this.flavorList = [];
      this.productList.forEach((item) => {
        if (this.orderOption.type === item.category) {
          this.flavorList.push(item);
        }
      });
      this.orderOption.flavor = this.flavorList[0].title;
      // eslint-disable-next-line prefer-destructuring
      this.tempProduct = this.flavorList[0];
    },
    selectSize() {
      this.flavorList.forEach((item) => {
        if (item.title === this.orderOption.flavor) {
          this.sizeList = item.options;
        }
      });
      // eslint-disable-next-line no-underscore-dangle
      this.orderOption.size = this.sizeList[0]._id;
      this.orderOption.price = this.sizeList[0].price;
    },
  },
  mounted() {
    this.getProducts();
  },
};
</script>
