<!-- eslint-disable vuejs-accessibility/anchor-has-content -->
<template>
  <section class="card-body p-0">
    <table class="table tableText tableSm table-bordered border-primary m-0">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">PRODUCT</th>
          <th scope="col">SIZE</th>
          <th scope="col">PRICE</th>
          <th scope="col">Qty</th>
          <th scope="col">SUB</th>
        </tr>
      </thead>
      <tbody class="table-group-divider luluText">
        <template v-for="(item, index) in cartList.carts" :key="index">
          <tr>
            <td>{{ index + 1 }}</td>
            <td>
              <div class="d-flex align-items-center text-sm-nowrap">
                <i
                  class="bi bi-circle-fill me-1"
                  :class="{
                    'text-danger': item.product.category === 'pie',
                    'text-info': item.product.category === 'cake',
                    'text-secondary': item.product.category === 'cookie',
                  }"
                  style="font-size: 8px"
                ></i
                >{{ item.product.title }}
              </div>
            </td>
            <template v-for="(data, key) in item.option" :key="key">
              <template v-if="key === 0">
                <td>{{ data.info.size }}</td>
                <td>${{ data.info.price }}</td>
                <td class="text-center">
                  <a
                    href="#"
                    class="me-1 text-decoration-none"
                    @click.prevent="addCart(-1, data, item)"
                    >-</a
                  ><span>{{ data.quantity }}</span
                  ><a
                    href="#"
                    class="ms-1 text-decoration-none"
                    @click.prevent="addCart(1, data, item)"
                    >+</a
                  >
                </td>
                <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
              </template>
            </template>
          </tr>
          <template v-for="(data, key) in item.option" :key="key">
            <tr v-if="item.option.length !== 1 && key !== 0">
              <td colspan="2"></td>
              <td>{{ data.info.size }}</td>
              <td>${{ data.info.price }}</td>
              <td class="text-center">
                <a
                  href="#"
                  class="me-1 text-decoration-none"
                  @click.prevent="addCart(-1, data, item)"
                  >-</a
                ><span>{{ data.quantity }}</span
                ><a
                  href="#"
                  class="ms-1 text-decoration-none"
                  @click.prevent="addCart(1, data, item)"
                  >+</a
                >
              </td>
              <td>${{ this.$toCurrency(data.quantity * data.info.price) }}</td>
            </tr>
          </template>
        </template>
        <tr>
          <td colspan="6" class="align-middle">輸入折價卷</td>
        </tr>
        <tr>
          <td colspan="2">
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-danger mb-0"></i>
              <h6>pie</h6>
            </div>
            <div class="d-flex align-items-center dashBottom">
              <i class="bi bi-dot h3 text-info mb-0"></i>
              <h6>cake</h6>
            </div>
            <div class="d-flex align-items-center">
              <i class="bi bi-dot h3 text-secondary mb-0"></i>
              <h6>cookie</h6>
            </div>
          </td>
          <td colspan="3" class="align-middle">total: ${{ this.$toCurrency(total) }}</td>
          <td colspan="1" class="text-sm-end text-center align-middle">
            <button class="btn btn-outline-primary rounded-0">
              <i class="bi bi-caret-right-fill"></i>CHECKOUT
            </button>
          </td>
        </tr>
      </tbody>
    </table>
  </section>
</template>

<style lang="scss" scoped>
.dashBottom {
  margin-bottom: -30px;
}
@media (min-width: 1024px) {
  .tableText {
    font-size: 24px;
  }
}
@media (max-width: 488px) {
  .dashBottom {
    margin-bottom: -15px;
  }
}
@media (max-width: 377px) {
  .tableSm > :not(caption) > * > * {
    padding: 0.25rem 0.25rem;
  }
}
</style>

<script>
// eslint-disable-next-line object-curly-newline
import { apiGetCarts, apiPostCart, apiDeleteCart, apiPatchCart } from '@/assets/javascript/api';

export default {
  data() {
    return {
      cartList: [],
      total: 0,
      finalTotal: 0,
    };
  },
  methods: {
    getCarts() {
      this.$emitter.emit('loadingStatus', true);
      apiGetCarts()
        .then((res) => {
          this.cartList = res.data.data;
          this.populateCarts(this.cartList.carts);
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
    addCart(num, option, item) {
      // 如果等於0執行刪除該品項
      if (option.quantity + num === 0) {
        // eslint-disable-next-line no-underscore-dangle
        const data = { productId: item.product._id, optionId: option._id };
        apiDeleteCart({ data })
          .then((res) => {
            console.log(res);
            this.getCarts();
          })
          .catch((err) => {
            console.log(err);
          });
      } else {
        // eslint-disable-next-line no-underscore-dangle
        const data = { productId: item.product._id, option: { size: option.size, quantity: num } };
        apiPostCart(data)
          .then((res) => {
            console.log(res);
            this.getCarts();
          })
          .catch((err) => {
            console.log(err);
          });
      }
    },
    patchCart() {
      apiPatchCart('data').then((res) => {
        console.log(res);
      });
    },
    populateCarts(data) {
      // 先將選項拉出，比對product裡頭的選項，將正確得填入選項
      data.forEach((item) => {
        item.option.forEach((e) => {
          item.product.options.forEach((index) => {
            // eslint-disable-next-line no-underscore-dangle
            if (index._id === e.size) {
              e.info = index;
            }
          });
        });
      });
      //   計算總金額
      this.total = 0;
      this.cartList.carts.forEach((item) => {
        item.option.forEach((option) => {
          this.total += option.info.price * option.quantity;
        });
      });
    },
  },
  mounted() {
    this.getCarts();
    this.$emitter.on('getCatrs', () => {
      this.getCarts();
    });
  },
};
</script>
